'use client'

import { TopInfoTaskAuthor } from "@/components/TopInfoTaskAuthor/TopInfoTaskAuthor"
import { Button } from "@/components/ui/Button/Button"
import { useState } from "react"


export default function page() {

    const [stateTabs, setStateTabs] = useState('process')

    return (
        <div className="flex flex-col w-screen">
            <div className="w-full">
                <TopInfoTaskAuthor />
            </div>
            <div className="flex justify-between gap-[0px] w-260 mb-4">
                <Button classname={`${stateTabs === 'process' ? 'border-l-3 border-[#2B7FFF] bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]' : ''} font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out`}>
                    <span className="text-gray-700">Задача в процессе</span>
                </Button>
                <Button classname={`font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]`}>
                    <span className="text-gray-700">Завершенные задачи</span>
                </Button>
                <Button classname={`font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]`}>
                    <span className="text-gray-700">Отменённые задачи</span>
                </Button>
                <Button classname={`font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]`}>
                    <span className="text-gray-700">Все задачи</span>
                </Button>
            </div>
            <div className="w-full h-full overflow-x-auto overflow-y-auto">
            
            </div>
        </div>
    )
}
