'use client'

import { useEffect, useState } from "react"
import { Button } from "../ui/Button/Button"
import { InputTaskInfo } from "../ui/InputTaskInfo/InputTaskInfo"
import { Select } from "../ui/Select/Select"
import { User } from "@/shared/types/user.types"
import { fetchConfig } from "@/api/fetchConfig"
import { api } from "@/pages/api/api"
import { APICOMMAND } from "@/shared/types/command.types"
import { Priority } from "@/shared/types/priority.types"
import { Status } from "@/shared/types/status.types"

interface Props { }

export function TopInfoTaskAuthor({ }: Props) {

    const [users, setUsers] = useState<User[] | null>(null)
    const [prioritys, setPrioritys] = useState<Priority[] | null>(null)
    const [status, setStatus] = useState<Status[] | null>(null)

    const options = [
        'Задача назначена',
        'Задача принята исполнителем',
        'Задача в работе',
        'Задача завершена',
        'Задача отменена',
        'Задача изменена',
    ]

    useEffect(() => {
        const fetchUsers = async () => {
            const config = await fetchConfig()
            const responseUsers = await api(APICOMMAND.GETUSERS, {}, config)
            const data = await responseUsers?.json()
            setUsers(data.data)
            const response = await api(APICOMMAND.GETPRIORITY, {}, config)
            const dataPriority = await response?.json()
            setPrioritys(dataPriority.data)
            const responseStatus = await api(APICOMMAND.GETSTATUS, {}, config)
            const dataStatus = await responseStatus?.json()
            setStatus(dataStatus.data)
        }
        fetchUsers()
    }, [])

    console.log(users)
    console.log(status)
    console.log(prioritys)

    return (
        <div className="w-full bg-[#F5F7FB]">
            <div className="flex gap-[20px] flex-wrap p-4 pl-6 w-300 items-end">
                <InputTaskInfo type="text" placeholder="Название задачи" label="Название задачи" />
                <InputTaskInfo type="date" placeholder="Дата создания задачи" label="Дата создания задачи" />
                <InputTaskInfo type="date" placeholder="Дата примерного окончания задачи" label="Дата примерного окончания задачи" />
                <Button classname="font-medium bg-blue-500 w-45 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out" type="submit">Прикрепить документы</Button>
                <InputTaskInfo type="text" placeholder="Автор постановки задачи" label="Автор постановки задачи" />
                <Select label={'Исполнитель'} options={users ? users : []} />
                <Select label={'Статус'} options={status ? status : []} />
                <Button classname="font-medium bg-blue-500 w-45 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out" type="submit">Добавить задачу</Button>
                <Button classname="font-medium bg-blue-500 w-45 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out" type="submit">Повторить задачу</Button>
                <InputTaskInfo type="text" placeholder="Дополнительная информация" />
                <Select label={'Статус'} options={prioritys ? prioritys : []} />
                <Button classname="font-medium bg-blue-500 w-45 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out" type="submit">Изменить приоритет</Button>
                <Button classname="font-medium bg-blue-500 w-45 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out" type="submit">Изменить задачу</Button>
            </div>
        </div>
    )
}

